import React, { Component } from 'react';

import { Button } from 'reactstrap';

import './helpers'

import Sudoku from './Sudoku'

class App extends Component {

  constructor() {
    super()
    this.state = this.getSudokuState()
    Sudoku.change(() => {
      this.setState(this.getSudokuState())
    })
  }

  getSudokuState() {
    return {
      data: Sudoku.getField(),
      status: Sudoku.getStatus()
    }
  }

  random() {
    Sudoku.random()
  }

  clear() {
    Sudoku.clear()
  }

  increase(i, j, event) {
    Sudoku.increase(i, j)
  }

  decrease(i, j, event) {
    if (event) {
      event.preventDefault()
    }
    Sudoku.decrease(i, j)
  }

  solve() {
    Sudoku.solve()
  }

  stop() {
    Sudoku.stop()
  }

  step() {
    Sudoku.step()
  }

  render() {
    return (
      <div className="App">
        {this.state.data.map((i, iIndex) => <div key={iIndex} className="row" >
          {i.map((j, jIndex) => <span
            key={jIndex}
            className={"cell " + j.type}
            onClick={this.increase.bind(this, iIndex, jIndex)}
            onContextMenu={this.decrease.bind(this, iIndex, jIndex)}
            >
            {j.value}
            </span>)}
        </div>)}
        <hr />
        {this.state.status}
        <hr />
        <Button onClick={this.random}>Random</Button>
        <Button onClick={this.clear}>Clear</Button>
        <Button onClick={this.stop}>Stop</Button>
        <Button onClick={this.solve}>Solve</Button>
        <Button onClick={this.step}>Step</Button>
      </div>
    );
  }
}

export default App;



