function getRandNum(size) {
  return Math.floor(Math.random() * size)
}

class Sudoku {
  constructor() {
    this.arEvents = []
    this.SIZE = 9
    this.getEmptyField()

    this.resetCoords()
  }

  change(fn) {
    this.arEvents.push(fn)
  }

  callback() {
    this.arEvents.map(fn => fn())
  }

  clear() {
    this.getEmptyField()
    this.stop()
  }

  stop() {
    if (this.interval) {
      clearInterval(this.interval)
    }
  }

  restart() {
    this.getEmptyField()
  }

  increase(i, j) {
    let cell = this.FIELD[i][j]
    if (cell.type === 'start') {
      // return null;
    }
    cell.value++
    if (cell.value > 9) {
      cell.value = 0
    }
    this.callback()
  }

  decrease(i, j) {
    let cell = this.FIELD[i][j]
    if (cell.type === 'start') {
      // return null;
    }
    cell.value--
    if (cell.value < 0) {
      cell.value = 9
    }
    this.callback()
  }

  getEmptyField() {
    let ar = []
    for (let i = 0; i < this.SIZE; i++) {
      ar[i] = []
      for (let j = 0; j < this.SIZE; j++) {
        ar[i][j] = {
          x: j,
          y: i,
          value: 0
        }
      }
    }
    this.FIELD = ar
    this.resetCoords()
    this.callback()
    return this.FIELD
  }

  random() {
    this.getEmptyField()
    this.setRandom()
  }

  setRandom() {
    /*
    for (let k = 0; k < 20; k++) {
      let i = getRandNum(this.SIZE)
      let j = getRandNum(this.SIZE)
      this.FIELD[i][j].value = getRandNum(this.SIZE) + 1
      this.FIELD[i][j].type = 'start'
    }
    */

    let arSquares = this.getSquare()
    arSquares.forEach((i, iIndex) => {
      let col = i[getRandNum(this.SIZE)]
      this.FIELD[col[0]][col[1]].value = getRandNum(this.SIZE) + 1
      this.FIELD[col[0]][col[1]].type = 'start'
    })

    this.callback()
  }

  getField() {
    return this.FIELD
  }

  getStatus() {
    if (this.canSolve()) {
      if (this.isSolved()) {
        return 'Solved!!!'
      } else {
        return 'Solving.'
      }
    } else {
      return 'Failed'
    }
  }

  isSolved() {
    if (!this.canSolve()) {
      return false
    }
    for (let i = 0; i < this.SIZE; i++) {
      for (let j = 0; j < this.SIZE; j++) {
        if (!this.FIELD[i][j].value) {
          return false
        }
      }
    }
    return true
  }

  canSolve() {
    let arr = this.FIELD
    for (let i = 0; i < this.SIZE; i++) {
      if (!this.checkArray(arr[i])) {
        return false
      }

      for (let j = 0; j < this.SIZE; j++) {
        if (!this.checkArray(arr.map(str => str[j]))) {
          return false
        }
      }
    }

    let arSquares = this.getSquare()
    let test = true
    arSquares.forEach((i, index) => {
      let testArr = []
      i.forEach(cell => {
        testArr.push(arr[cell[0]][cell[1]])
      })
      if (!this.checkArray(testArr)) {
        test = false
      }
    })
    if (!test) {
      return false
    }

    return true
  }

  checkArray(arr) {
    arr = arr.map(data => data.value)
    arr = arr.filter(data => data)
    let sort = arr.sort()
    let uniq = sort.uniq()
    return uniq.length === sort.length
  }

  getSquare() {
    let arSquares = []

    for (let i = 0; i < 9; i++) {
      arSquares[i] = []
      let start = Math.floor(i / 3)
      let add = start * 3
      let i3 = i * 3
      for (let j = 0; j < 9; j++) {
        let ii = add
        let jj = j + i3

        while (jj > 2 + i3) {
          ii++
          jj -= 3
        }

        while (jj >= 9) {
          jj -= 9
        }

        arSquares[i][j] = [ii, jj]
      }
    }
    return arSquares;
  }

  resetFields() {
    for (let i = 0; i < this.SIZE; i++) {
      for (let j = 0; j < this.SIZE; j++) {
        if (this.FIELD[i][j] && this.FIELD[i][j].type !== 'start') {
          this.FIELD[i][j].value = 0
        }
      }
    }
  }

  solve() {
    this.stop()
    this.resetCoords()
    this.resetFields()
    this.step({recursive: true})
  }

  step(params = {}) {
    if (this.isSolved()) {
      this.stop()
      return true
    }

    let cell = this.getCell()
    if (cell.type !== 'start') {
      if (cell.value < this.SIZE) {
        cell.value++
        if (this.canSolve()) {
          this.nextCell()
        }
      } else {
        cell.value = 0
        this.prevCell()
      }
    } else {
      this.nextCell()
    }

    this.callback()
    if (params.recursive) {
      this.interval = setTimeout(() => this.step({recursive: true}), 0)
    }
  }

  getCell() {
    console.log(this)
    return this.FIELD[this.y][this.x]
  }

  nextCell() {
    this.x++
    if (this.x >= this.SIZE) {
      this.x = 0
      this.y++
    }
    if (this.y >= this.SIZE) {
      this.prevCell()
    }
    if (this.getCell().type === 'start') {
      this.nextCell()
    }
  }

  prevCell() {
    this.x--
    if (this.x < 0) {
      this.x = this.SIZE - 1
      this.y--
    }
    if (this.getCell().type === 'start') {
      this.prevCell()
    }
  }

  resetCoords() {
    this.x = 0
    this.y = 0
  }

  block(i, j) {
    let cell = this.FIELD[i][j]
    cell.type = cell.type === 'start' ? null : 'start'
    this.callback()
  }
}

export default new Sudoku()



